
// benefits section style

.benefits {
    .benefits-header {
        width: 100%;
        height: 70px;
        background: $mainColor;
        h2 {
            color: #fff;
            line-height: 70px;
            font-weight: normal;
            margin: 0 !important;
            padding: 0 !important;
            @media (max-width: 767px) {
                font-size: 1.5rem;
            }
            @media (max-width: 500px) {
                font-size: 1.2rem;
            }
        }
    }
    .benefit-group {
        padding: 4rem 0;
        margin: 0 2rem;
        border-bottom: 1px solid rgba(0, 0, 0, .2);
        &:last-of-type {
            border-bottom: 0;
        }
        .benefit {
            padding-top: 2rem;
            .icon {
                width: 50px;
                margin: auto;
                img {
                    max-width: 100%;
                }
            }
            .title{
                .count {
                    font-size: 2.5rem;
                    font-weight: bold;
                    padding-right: .2rem;
                }
                .benefit-title {
                    h3 {
                        font-size: 1.1rem;
                        font-weight: bold;
                        margin: 0 !important;
                        padding: 0 !important;
                        @media (max-width: 500px) {
                            font-size: .9rem;
                        }
                    }
                    h4 {
                        font-size: 1.3rem;
                        font-weight: normal;
                        margin: 0 !important;
                        padding: 0 !important;
                        @media (max-width: 500px) {
                            font-size: 1rem;
                        }
                    }
                }
            }
            .description p {
                font-size: 1.1rem;
                color: #777;
                margin: 0;
                padding: 0;
                @media (max-width: 500px) {
                    font-size: .9rem;
                }
            }
        }
    }
}