
// header style 

header {
    nav.navbar {
        position: fixed;
        width: 100%;
        background: transparent;
        padding: 1.5rem 4rem;
        z-index: 333;
        transition: all .2s linear;
        @media (max-width: 767px) {
            padding: 1rem 2rem;
        }
        &.scrolled {
            box-shadow: 0 0 3px rgba(0, 0, 0, .1);
            background: #fff;
        }
        a.navbar-brand {
            width: 200px;
            padding: 0 !important;
            @media (max-width: 400px) {
                width: 120px;
            }
        }
        .navbar-toggler {
            border-color: #ccc;
            .navbar-toggler-icon {
                @media (max-width: 400px) {
                    width: 20px;
                    height:20px;
                }
            }
        }
        .navbar-nav {
            @media (max-width:992px) {
                text-align: center;
                margin-top: 20px;
                background: #fff;
                border-bottom: 1px solid rgba(0, 0, 0, .2);
            }
            .nav-item {
                a {
                    position: relative;
                    display: inline-block !important;
                    color: $mainColor !important;
                    margin-left: 1.5rem !important;
                    font-size: .9rem;
                    padding: 0 0 .5rem !important;
                    transition: all 2s linear;
                    @media (min-width: 993px) {
                        &.active {
                            &:after {
                                content: '';
                                position: absolute;
                                bottom: 5px;
                                left: 0;
                                width: 100%;
                                height: 1px;
                                background: $mainColor;
                                transition: all .2s linear;
                            }
                        }
                    }
                    @media (max-width: 992px) {
                        margin-left: 0 !important;
                        padding: 1rem 0 !important;
                    }
                }
                &:first-of-type a {
                    margin-left: 0 !important;
                }
            }
            .sign {
                a.sign-btn {
                    display: inline-block;
                    color: $mainColor;
                    margin-left: 1.5rem;
                    margin-top: -.2rem;
                    font-size: .9rem;
                    color: $mainColor;
                    border: 1px solid $mainColor;
                    padding: .2rem .5rem;
                    transition: all .2s linear;
                    @media (max-width:992px) {
                        margin: 0;
                    }
                }
                &:hover a {
                    background: $mainColor;
                    color: #fff !important;
                }
                @media (max-width:992px) {
                    text-align: center;
                    padding: 1rem 0;
                }
            }
        }
    }
}