
// how works style

.how-header nav.navbar {
    background: transparent !important;
    .navbar-nav {
        @media (max-width: 992px) {
            background: #fff !important;
            padding: 1rem 0;
        }
        .nav-item {
            a {
                color: #fff !important;
                    @media (max-width: 992px) {
                    color: $mainColor !important;
                    }
                &:after {
                    background: #fff !important;
                    @media (max-width: 992px) {
                        color: $mainColor !important;
                    }
                }
            }
        }
        .sign {
            a.sign-btn {
                color: #fff !important;
                border: 1px solid #fff !important;
                @media (max-width: 992px) {
                    color: $mainColor !important;
                    border: 1px solid $mainColor !important;
                }
            }
            &:hover a {
                background: #fff !important;
                color: $mainColor !important;
            }
        }
    }
}

section.how-banner {
    padding: 4rem 0;
    background: $mainColor;
    .how-text {
        h1 {
            color: #fff;
            font-size: 1.8rem;
            margin: 1rem 0 0 !important;
            @media (max-width: 575px) {
                font-size: 1.5rem;
            }
            @media (max-width: 400px) {
                font-size: 1.2rem;
            }
        }
        .how-img-screen {
            width: 650px;
            @media (max-width: 767px) {
                width: 350px;
            }
            @media (max-width: 400px) {
                width: 250px;
            }
        }
        h3 {
            display: flex;
            align-items: center;
            font-size: 2rem;
            @media (max-width: 767px) {
                font-size: 1.5rem;
            }
            @media (max-width: 400px) {
                font-size: 1.1rem;
            }
            span {
                color: #ccc;
            }
            img {
                width: 55px;
                margin-right: .7rem;
                @media (max-width: 767px) {
                    width: 35px;
                }
                @media (max-width: 400px) {
                    width: 25px;
                }
            }
        }
        p {
            font-size: 1.1rem;
            text-align: center;
            padding: 0 !important;
            margin: 0 !important;
            @media (max-width: 767px) {
                font-size: .9rem;
            }
            @media (max-width: 400px) {
                font-size: .8rem;
                padding: 0 2rem;
            }
        }
    }
    &:nth-of-type(2) {
        background: #005072;
    }
    &:nth-of-type(3) {
        background: #003c64;
    }
    &:nth-of-type(4) {
        background: #1e325f;
    }
    &:nth-of-type(5) {
        background: #443f63;
    }
}