
//  features style

.features {
    .header-features,
    .footer-features {
        background: $mainColor;
        width: 100%;
        height: 70px;
        color: #fff;
        text-align: center;
        h3 {
            margin: 0;
            line-height: 70px;
            font-weight: normal;
            @media (max-width: 767px) {
                font-size: 1.5rem;
            }
            @media (max-width: 500px) {
                font-size: 1.2rem;
            }
        }
    }
    .features-section {
        max-width: 1200px;
        width: 100%;
        margin: auto;
        img {
            max-width: 100%;
        }
    }
}    
    //  customize feature section style
    .list-feature {
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid rgba(0, 0, 0, .1);
        margin: 0 4rem;
        padding: 4rem 0;
        @media (max-width: 500px) {
            margin: 0;
            padding: 2rem 0;
        }
        .row {
            margin: 0 !important;
            .feature {
                padding: 0 1.5rem;
                background: linear-gradient(to right, #d5e6ee 10%, transparent 90%);
                @media (max-width: 400px) {
                    padding: 0 .5rem;
                }
                span {
                    font-size: 3rem;
                    font-weight: bold;
                    @media (max-width: 575px) {
                        font-size: 2.2rem;
                    }
                }
                .feature-icon {
                    width: 80px;
                    margin: 0 1rem;
                    img{
                        max-width: 100%;
                    }
                }
                .feature-title {
                    h3 {
                    color: $mainColor;
                    font-size: 1.2rem;
                    font-weight: bold;
                    margin: 0;
                    padding: 0 !important;
                    @media (max-width: 575px) {
                        font-size: 1rem;
                    }
                    }
                    h4 {
                        color: $mainColor;
                        font-size: 1rem;
                        margin: 0;
                        padding: 0 !important;
                    }
                }
            }
            .description-feature {
                margin-top: 3rem;
                @media (max-width: 767px) {
                    text-align: center;
                }
                h3 {
                    color: $mainColor;
                    @media (max-width: 575px) {
                        font-size: 1.5rem;
                    }
                    @media (max-width: 400px) {
                        font-size: 1.2rem;
                    }
                }
                h5 {
                    font-size: 1rem;
                    font-weight: normal;
                }
                p {
                    font-size: .9rem;
                    color: #434345;
                    @media (max-width: 575px) {
                        font-size: .8rem;
                        text-align: left;
                    }
                }
            }
            .right {
                @media (max-width: 992px) {
                    margin-top: 2rem;
                }
                .caption {
                    h2 {
                        color: $mainColor;
                        text-align: center;
                        margin-bottom: 3rem;
                        @media (max-width: 992px) {
                            display: none;
                        }
                    }
                }
                .customize-img {
                    width: 70%;
                    margin: auto;
                    @media (max-width: 992px) {
                        width: 50%;
                        margin-right: auto;
                    }
                    @media (max-width: 767px) {
                        width: 70%;
                        margin: auto;
                    }
                    img {
                        max-width: 100%;
                        }
                }
            }
        }
    }