
// home style
.banner {

    .hero {
        position: relative;
        height: 100vh;
        background: linear-gradient(rgba(255, 255, 255, .8), rgba(255, 255, 255, .8)), url('../img/bannerBG.png') center no-repeat;
        background-size: cover;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        transform: translate(0, -10%);
        @media (max-width: 767px) {
            height: auto;
            transform: translate(0);
        }
        &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -10px;
            width: 100%;
            height: 10%;
            background: linear-gradient(rgba(255, 255, 255, .1),  rgba(255, 255, 255, .4),  rgba(255, 255, 255, .7), rgba(255, 255, 255, .9));
            @media (max-width: 767px) {
                content: none;
            }
        }
        h1 {
            font-weight: 100 !important;
            color: $mainColor;
            @media (max-width:992px) {
                font-size: 2rem;
            }
            @media (max-width:767px) {
                font-size: 1.5rem;
            }
            @media (max-width:575px) {
                font-size: 1.2rem;
            }
        }
        form {
            margin-top: 2.5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            .email {
                display: block;
                width: 23%;
                input {
                    width: 100%;
                    padding: .7rem;
                    font-size: .9rem;
                    color: #f5f0f0e1;
                    outline: none;
                    border: 1px solid $mainColor;
                }
                @media (max-width:992px) {
                    width: 35%;
                }
            }
            .check-demo-btn {
                width: 23%;
                button {
                    width: 100%;
                    padding: .7rem;
                    font-size: .9rem;
                    background: $mainColor;
                    border: 1px solid $mainColor;
                    outline: none;
                    transition: all .2s linear;
                }
                @media (max-width:992px) {
                    width: 35%;
                }
                @media (max-width: 400px) {
                    font-size: .7rem;
                    width: 40%;
                }
            }
        }
    }
    
    .box-intro {
        margin-top: 2.5rem;
        h4 {
            color: $mainColor;
            @media (max-width: 400px) {
                font-size: 1.1rem;
            }
        }
        p {
            font-size: .9rem;
            color: #434345;
        }
    }
    .our-workflow-advantages {
        .advantages {
            margin-top: 3.5rem;
            .advantage {
                .icon {
                    width: 90px;
                    margin-bottom: 1rem;
                    img {
                        max-width: 100%;
                    }
                    @media (max-width:767px) {
                        width: 50px;
                    }
                }
                h3 {
                    font-size: 1.3rem;
                    color: $mainColor;
                    margin: 0;
                    @media (max-width:767px) {
                        font-size: 1rem;
                    }
                    @media (max-width:400px) {
                        font-size: .7rem;
                    }
                }
                h2 {
                    font-size: 1rem;
                    color: $mainColor;
                    font-weight: bold;
                    margin: 0;
                    @media (max-width:767px) {
                        font-size: .8rem;
                    }
                    @media (max-width:400px) {
                        font-size: .6rem;
                    }
                }
            }
        }
        p {
            font-size: .9rem;
            margin-top: 3rem;
        }
        hr {
            width: 40%;
            margin: 3rem auto 0;
        }
    }
}