

//  process style

.processes {
    padding: 5rem 0;
    @media (max-width: 767px) {
        padding: 2rem 0;
    }
    h2 {
        color: $mainColor;
        margin-bottom: 2rem;
        @media (max-width:767px) {
            font-size: 1.3rem;
        }
        @media (max-width:455px) {
            font-size: .9rem;
        }
    }
    .workflow-process{
        .process {
            .icon {
                background: $mainColor;
                width: 100px;
                height: 100px;
                border-radius: 100%;
                text-align: center;
                color: #fff;
                line-height: 90px;
                font-size: 2.2rem;
                margin-bottom: 1rem;
                span {
                    display: inline-block;
                    padding-right: .5rem;
                    font-size: 2.8rem;
                    @media (max-width:767px) {
                        font-size: 1rem;
                    }
                }
                @media (max-width:767px) {
                    width: 60px;
                    height: 60px;
                    line-height: 60px;
                    font-size: 1.1rem;
                }
                @media (max-width: 400px) {
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    font-size: .8rem;
                }
            }
            h4 {
                font-size: 1.3rem;
                color: $mainColor;
                @media (max-width:767px) {
                    font-size: .8rem;
                }
                @media (max-width:500px) {
                    font-size: .7rem;
                }
                @media (max-width:400px) {
                    font-size: .6rem;
                }
            }
            h3 {
                font-size: 1rem;
                font-weight: bold;
                color: $mainColor;
                @media (max-width:767px) {
                    font-size: .7rem;
                }
                @media (max-width:500px) {
                    font-size: .6rem;
                }
                @media (max-width:400px) {
                    font-size: .5rem;
                }
            }
        }
    }
}